<template>
  <div style="padding-top: 2.5rem;">
    <transition name="aside">
      <userSetting
        v-show="setting">
      </userSetting>
    </transition>
    <div class="shadow"
         v-show="setting"
         @click="setting = false">
    </div>
    <header class="header-warpper">
      <div class="header-setting"
           @click.stop="setting = true">
        <i class="icon-setting">&#xe667;</i>
      </div>
      <div class="header-menu">
        <i :class="['icon-music',{checked:type === 'music'}]"
           @click="nav(1)">
          &#xe680;
        </i>
        <i :class="['icon-netease',{checked:type === 'find'}]"
           @click="nav(2)">
          &#xe658;
        </i>
        <i class="icon-friend">&#xe609;</i>
      </div>
      <div class="header-search" @click="$router.push('/musicSearch')">
        <i class="icon-search">&#xe63b;</i>
      </div>
    </header>
    <transition name="slide">
      <router-view/>
    </transition>
    <keep-alive>
      <musicDetail></musicDetail>
    </keep-alive>

  </div>
</template>

<script>
  // import userSetting from '@/view/modules/userSetting.vue';
  import test from '@/api/test.js';
  const userSetting = resolve => require(['@/view/layout/components/userSetting'],resolve);
  const musicDetail = resolve => require(['@/view/layout/components/musicDetail'],resolve);
  export default {
    name: "index",
    components: {
      userSetting,
      musicDetail
    },
    data() {
      return {
        setting: false,
        type: 'find'
      }
    },
    mounted:function () {
      test();
    },
    computed:{
        dataA() {
          return this.$store.state.moduleA.data;
        }
    },
    methods: {
      nav(idx) {
        if (idx === 1) {
          this.$router.push('/index/myMusic');
          this.type = 'music'
        }
        else {
          this.$router.push('/index/findMusic');
          this.type = 'find'
        }
        console.log(this.dataA);
        this.$store.getters.printData;
      }
    }
  }
</script>

<style scoped>
  .slide-enter-active, .slide-leave-active {
    transition-duration: .2s;
  }

  .slide-enter, .slide-leave-to {
    transform: translateX(-100%);
    opacity: 0;
  }

  .aside-enter-to, .aside-leave-to {
    transition: all .3s;
  }

  .aside-enter, .aside-leave-to {
    transform: translateX(-100%);
  }

  .checked {
    color: #68cffa !important;
  }

  .shadow {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 101;
    background-color: rgba(0, 0, 0, 0.44);
  }
</style>



